<template>
    <div class="home">
        <PageTitle icon="fa fa-home" main="Dashboard"
            sub="Watashi No Kaimono" />
        <div class="stats">
            <Stat title="Categorias" :value="stat.categories"
                icon="fa fa-folder" color="#d54d50" />
            <Stat title="Subcategorias" :value="stat.subcategories"
                icon="fa fa-file" color="#d54d50" />
            <Stat title="Produtos" :value="stat.products"
                icon="fa fa-list" color="#3bc480" />
            <Stat title="Usuários" :value="stat.users"
                icon="fa fa-user" color="#3282cd" />
        </div>
    </div>
</template>

<script>
import PageTitle from '@/components/template/PageTitle'
import Stat from './Stat'
import axios from 'axios'
import { baseApiUrl } from '@/global'

export default {
    name: 'Home',
    components: { PageTitle, Stat },
    data: function(){
        return {
            stat: {}
        }
    }, methods: {
        getStats() {
            axios.get(`${baseApiUrl}/api/v1/stats`).then(res => this.stat = res.data)
        }
    },
    mounted() {
        this.getStats()
    }
}
</script>

<style>
    .stats {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
</style>